@import "../../main.scss";


.wrapper {
    position: absolute;
    width: 100%;
}

.section {
    position: relative;
    height: 100vh;
    overflow: hidden;
}

.treeBackground {
    position: absolute;
    top: -30vh;
    left: 50vw;
    z-index: -10;

}

.container {
    margin-bottom: 0;
    padding: 80px 80px;

    @include desktop1440 {
        padding: 80px 40px;
    }
}

.description {
    margin-top: 50px;
    max-width: 550px;
    color: $thirdColor;
}

.title {
    font-size: 48px;
    margin-bottom: 30px;
}

.descriptionText {
    font-size: 20px;
    font-weight: 400;
}