@import "../../main.scss";


.wrapper {
    position: relative;
    margin-right: 15px;

    &:last-child {
        margin-right: 0;
    }
}

.alert {
    position: absolute;
    display: none;
    justify-content: center;
    align-items: center;
    height: 16px;
    width: 16px;
    border-radius: 8px;
    background-color: $firstColor;
    z-index: 100;
    right: -3px;
    top: -3px;
    color: $seventhColor;
    font-weight: 700;

}

.active {
    display: flex
}


.container {
    position: relative;
    height: 40px;
    width: 40px;
    border-radius: 20px;
    background-color: $fifthColor;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
    transition: background-color .5s;
    
    &:hover {
        cursor: pointer;
        background-color: $fourthColor;
    
    }
}

.icon {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    
    &.user {
        top: 10%;
    }
}